<template>
  <div>
    <b-breadcrumb class="mb-0" :items="breadcrumb"></b-breadcrumb>
    <b-container fluid>
      <b-form class="otherForm increase-block mt-5">
        <b-row>
          <b-col>
            <b-form-group
              label="標題"
              label-class="main-title"
              label-cols-lg="2"
            >
              <b-form-input
                id="input-1"
                v-model="NewsList['NE_TITLE']"
                type="text"
                placeholder="請輸入標題"
                required
              ></b-form-input>
            </b-form-group>
            <b-form-group
              label="日期"
              label-class="main-title"
              label-cols-lg="2"
            >
              <b-form-input
                id="input-1"
                v-model="date"
                type="date"
                placeholder="請輸入日期"
                required
              ></b-form-input>

            </b-form-group>
            <b-form-group
              label="內容"
              label-class="main-title"
              label-cols-lg="2"
            >
              <tinymceEditor v-model="NewsContent"></tinymceEditor>
            </b-form-group>

            <b-form-group
              label="是否上線"
              label-class="main-title"
              label-cols-lg="2"
            >
              <b-form-checkbox
                v-model="switchvalue"
                name="check-button"
                switch
                size="lg"
              ></b-form-checkbox>
            </b-form-group>

            <b-form-group
              label="附件"
              label-class="main-title"
              label-cols-lg="2"
            >
              <InputV1
                :type="form1.data['file1'].type"
                :data_in="form1.data['file1']"
              ></InputV1>
            </b-form-group>

            <ul>
              <li v-for="(item, i) in filesdata" :key="i">
                <div class="row">
                  <div class="col-lg-2"></div>
                  <div>{{ item.filename }}</div>
                  <button
                    class="btn=xs btn-info ml-3 p-1 mb-1"
                    @click="clear(i)"
                  >
                    刪除
                  </button>
                </div>
              </li>

              <li v-for="(item, i) in filesList" :key="i">
                <div class="row">
                  <div class="col-lg-2"></div>
                  <div>{{ item.MC_NAME }}</div>
                  <button
                    class="btn=xs btn-info ml-3 p-1 mb-1"
                    @click="clear1(item.MC_ID, i)"
                  >
                    刪除
                  </button>
                </div>
              </li>
            </ul>

            <b-row>
              <b-col offset-lg="2" class="py-3">
                <b-button variant="primary" size="lg" @click="form1Save()"
                  >儲存</b-button
                >
              </b-col>
            </b-row>
          </b-col>
        </b-row>
      </b-form>
    </b-container>
  </div>
</template>

<script>
import AES from "@/store/aes.js";
import InputV1 from "../../components/InputV1.vue";
import tinymceEditor from "../../components/tinymce-editor/tinymce-editor.vue";

export default {
  data() {
    return {
      date: "",
      switchvalue: false,
      NewsList: [],
      NewsContent: null,
      files: [],
      filesList: [], //已新增檔案
      filesdata: [], //此次新增檔案
      filesListDelete: [], //已新增檔案刪除
      breadcrumb: [
        {
          text: "最新消息",
          to: "/Admin_Index",
        },
        {
          text: "編輯消息內容",
          active: true,
        },
      ],
      form1: {
        title: "消息內容",
        data: {
          NE_TITLE: {
            type: "text",
            val: "",
            text: "請填寫標題",
            only: false,
            msg: "",
          },
          NE_DATE: { type: "date", val: "", only: false, msg: "" },
          NE_CONTENT: { val: "" },
          NE_ISONLINE: {
            type: "b-switch-group",
            val: [],
            only: false,
            size: "lg",
            stacked: false,
            options: [{ value: "1", text: "" }],
            msg: "",
          },
          file1: {
            type: "file2",
            val: "",
            text: "上傳",
            only: false,
            id: "upfile1",
            name: "請選取檔案",
            name2: "請選取檔案",
            even: "",
            msg: "",
            accept: "",
            multiple: false,
            img: { type: "", id: "", name: "" },
          },
        },
      },
      apiUpdateNews: (data) =>
        this.userRequest.post("API/Default/UpdateNews", data),
      ApiGetNewsSingle: (data) =>
        this.userRequest.post("API/Default/GetNewsSingle", data),
      ApiGetNewsFile: (data) =>
        this.userRequest.post("API/Default/GetArchive", data),
    };
  },
  components: {
    InputV1,
    tinymceEditor,
  },
  created() {
    this.userSignCheck();
    this.Getsession();
    // this.getNewsInfo
  },
  mounted() {
    this.form1.data["NE_DATE"].val = this.date2str(new Date()).join("-");
    this.form1.data["file1"].change = (data) => {
      let file = this.even2file(data.even);
      if (
        file[0].file.name.split("，").length < 2 &&
        file[0].file.name.split("！").length < 2 &&
        file[0].file.name.split("。").length < 2 &&
        file[0].file.name.split("、").length < 2
      ) {
        if (file[0].file.size < 5000000) {
          if (file.length != 0) {
            this.files.push(file[0]);
            //console.log(file);
            this.functionBase64(file[0]); //取得Base64
          }
        } else {
          alert("上傳檔案上限為5Mb");
        }
      } else {
        alert("檔案名稱不得加入，、。!");
      }
      this.form1.data["file1"].fileClear(this.form1.data["file1"]);
      //超過五筆檔案鎖定上傳
      if (this.filesdata.length + this.filesList.length > 3) {
        this.form1.data["file1"].only = true;
      }
    };
  },
  methods: {
    //移除陣列
    clear(index) {
      this.filesdata.splice(index, 1);
      this.files.splice(index, 1);
      //開啟上傳檔案功能
      if (this.filesdata.length + this.filesList.length < 5) {
        this.form1.data["file1"].only = false;
      }
    },
    //移除陣列
    clear1(MC_ID, index) {
      // console.log(MC_ID);
      // console.log(index);
      this.filesListDelete.push(MC_ID);
      this.filesList.splice(index, 1);
      // console.log(this.filesListDelete);
      // console.log(this.filesList);
      //開啟上傳檔案功能
      if (this.filesdata.length + this.filesList.length < 5) {
        this.form1.data["file1"].only = false;
      }
    },
    //取得Base64
    functionBase64(item) {
      this.getBase64(item.file).then(
        (success) => {
          this.fileobj = {
            filename: item.name,
            filebase64: success,
          };
          this.filesdata.push(this.fileobj);
          console.log(this.filesdata);
          // console.log(this.filesdata[0].filebase64);
          // console.log(this.filesdata[0].filename);
        },
        (fail) => {
          console.log(fail);
        }
      );
    },
    //轉換Base64
    async getBase64(file) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => {
          let encoded = reader.result
            .toString()
            .replace(/^data:(.*,)?/, "")
            .replace(/^.*;base64,/, "");
          if (encoded.length % 4 > 0) {
            encoded += "=".repeat(4 - (encoded.length % 4));
          }
          resolve(encoded);
        };
        reader.onerror = (error) => reject(error);
      });
    },
    even2file(even) {
      let out = [];
      for (let i = 0; i < even.target.files.length; i++) {
        let item = even.target.files[i];
        out.push({ name: item.name, file: item });
      }
      return out;
    },
    userSignCheck() {
      if (sessionStorage.getItem("TokenID") === null) {
        alert("請登入系統!!");
        this.$router.push({ path: "/Login" });
        // console.log("success");
      }
    },
    //取得資料
    Getsession() {
      // const Newsdata = this.$route.query.name;
      // console.log(this.$route.query.name["NE_ID"]);
      const NE_ID = this.$route.query.name["NE_ID"];
      this.FunctionToken(this.GetNewsSingle, { NE_ID: NE_ID });
    },
    //新增檢查
    checkInput() {
      this.errorText = "";
      if (this.NewsList["NE_TITLE"] == "") this.errorText += "請輸入標題\n";
      if (this.NewsList["NE_DATE"] == "") this.errorText += "請輸入日期\n";
      if (this.NewsContent == "") this.errorText += "請輸入內容 \n";
      if (this.errorText !== "") alert(this.errorText);
    },
    form1Save() {
      this.checkInput();
      if (this.errorText === "") {
        let json1 = {};
        json1.NE_ID = this.$route.query.name["NE_ID"];
        json1.NE_TITLE = this.NewsList["NE_TITLE"];
        json1.NE_DATE = this.date;
        json1.NE_HTID = "";
        var aesMStr = null;
        aesMStr = AES.encrypt(this.NewsContent); // 加密
        json1.NE_CONTENT = aesMStr;
        json1.NE_ISONLINE = this.switchvalue == true ? 1 : 0;
        json1.NE_MC_ID_DEL = this.filesListDelete;
        //初始值
        var data = {};
        data.URL = [];

        for (let i = 0; i < this.filesdata.length; i++) {
          var NE_URL = new Object();
          NE_URL.URL_NAME = this.filesdata[i].filename;
          NE_URL.ICON_URL = this.filesdata[i].filebase64;

          data.URL.push(NE_URL);
        }
        json1.NE_URL = data.URL;

        console.log(json1);
        this.FunctionToken(this.FunctionUpdateNews, json1);
      }
    },
    //=================================================
    //api
    GetNewsSingle(data) {
      this.ApiGetNewsSingle(data)
        .then((res) => {
          let json = JSON.parse(res.data);
          if (json.Status) {
            let data = JSON.parse(json.Data);
            //判定是否有資料 //有錯誤
            if (data === null) {
              console.log("datanull");
              this.$router.push({ path: "/Admin_Index" });
            } else {
              this.NewsList = data;
              var aaa = AES.decrypt(this.NewsList["NE_CONTENT"]);
              this.NewsContent = aaa; // 解密
              this.switchvalue =
                this.NewsList["NE_ISONLINE"] == 1 ? true : false;
              this.date = this.NewsList["NE_DATE"].split("T")[0];

              //取得檔案
              if (this.NewsList.NE_MCID != null) {
                //console.log(this.NewsList.NE_MCID);
                let NE_MCID = [];
                NE_MCID = this.NewsList.NE_MCID.split(",");
                this.FunctionToken(this.GetNewsFile, {
                  MC_ID: NE_MCID,
                });
              }
            }
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    GetNewsFile(data) {
      this.ApiGetNewsFile(data)
        .then((res) => {
          let json = JSON.parse(res.data);
          if (json.Status) {
            let data = JSON.parse(json.Data);
            this.filesList = data;
            // this.switchvalue = this.NewsList["NE_ISONLINE"] == 1 ? true : false;
            // this.date = this.NewsList["NE_DATE"].split("T")[0];
            //console.log(this.filesList);
            //超過五筆檔案鎖定上傳
            if (this.filesList.length > 3) {
              this.form1.data["file1"].only = true;
            }
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    FunctionUpdateNews(data_in) {
      this.apiUpdateNews(data_in)
        .then((res) => {
          let json = JSON.parse(res.data);
          if (json.Status) {
            alert("更新成功!");
            this.$router.push({ path: "/Admin_Index" });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>