<template>
  <div class="tinymce-editor" style="">
    <editor v-model="myValue"
      :init="init"
      :disabled="disabled"
      @onClick="onClick">
    </editor>
  </div>
</template>
<script>
import tinymce from 'tinymce/tinymce'
import Editor from '@tinymce/tinymce-vue'
import 'tinymce/themes/silver'
import 'tinymce/icons/default/icons'
import 'tinymce/plugins/image'
import 'tinymce/plugins/media'
import 'tinymce/plugins/table'
import 'tinymce/plugins/lists'
import 'tinymce/plugins/link'
import 'tinymce/plugins/wordcount'
import 'tinymce/plugins/code'




export default {
  components: {
    Editor
  },
  props: {
    value: {
      type: String,
      default: ''
    },
    // 基本路径，預設為空根目錄，如果你的項目發佈後的網址為目錄型式，
    // 如abc.com/tinymce，baseUrl需要配置成tinymce，不然發佈後套件會無法使用
    baseUrl: {
      type: String,
      default: ''
    },
    disabled: {
      type: Boolean,
      default: false
    },
    plugins: {
      type: [String, Array],
      default: 'lists image media table wordcount link code '
    },
    toolbar: {
      type: [String, Array],
      default: 'undo redo |  formatselect | bold italic forecolor backcolor | alignleft aligncenter alignright alignjustify | lists image media link |'
      // default: 'undo redo |  formatselect | bold italic forecolor backcolor | alignleft aligncenter alignright alignjustify | bullist numlist outdent indent | lists image media table | removeformat link code '
    }
  },
  data () {
    return {
      init: {
        language_url: this.baseUrl+'/tinymce/langs/zh_TW.js',
        language: 'zh_TW',
        skin_url: this.baseUrl+'/tinymce/skins/ui/oxide',
        content_css: this.baseUrl+'/tinymce/skins/content/default/content.css',        
        height: 560,
        plugins: this.plugins,
        toolbar: this.toolbar,
        branding: false,
        menubar: false,        
        images_upload_handler: (blobInfo, success) => {
          const img = 'data:image/jpeg;base64,' + blobInfo.base64()
          success(img)
        }
      },
      myValue: this.value
    }
  },
  mounted () {
    tinymce.init({})
  },
  methods: {    
    onClick (e) {
      this.$emit('onClick', e, tinymce)
    },    
    clear () {
      this.myValue = ''
    }
  },
  watch: {
    value (newValue) {
      this.myValue = newValue
    },
    myValue (newValue) {
      this.$emit('input', newValue)
    }
  }
}
</script>