<template>
  <div>
    <b-breadcrumb class="mb-0" :items="breadcrumb"></b-breadcrumb>
    <b-container fluid>
      <b-form class="otherForm increase-block mt-5">
        <b-row>
          <b-col>
            <b-form-group
              label="標題"
              label-class="main-title"
              label-cols-lg="2"
            >
              <InputV1
                :type="form1.data['NE_TITLE'].type"
                :data_in="form1.data['NE_TITLE']"
              ></InputV1>
            </b-form-group>
            <b-form-group
              label="日期"
              label-class="main-title"
              label-cols-lg="2"
            >
              <InputV1
                :type="form1.data['NE_DATE'].type"
                :data_in="form1.data['NE_DATE']"
              ></InputV1>
            </b-form-group>
            <b-form-group
              label="內容"
              label-class="main-title"
              label-cols-lg="2"
            >
              <tinymceEditor
                v-model="form1.data['NE_CONTENT'].val"
              ></tinymceEditor>
            </b-form-group>

            <b-form-group
              label="是否上架"
              label-class="main-title"
              label-cols-lg="2"
            >
              <InputV1
                :type="form1.data['NE_ISONLINE'].type"
                :data_in="form1.data['NE_ISONLINE']"
              ></InputV1>
            </b-form-group>
            <b-row>
              <b-col offset-lg="2" class="py-3">
                <b-button variant="primary" size="lg" @click="form1Save()"
                  >儲存</b-button
                >
              </b-col>
            </b-row>
          </b-col>
          <b-form-textarea v-model="json" rows="8"></b-form-textarea>
        </b-row>
      </b-form>
    </b-container>
  </div>
</template>

<script>
import AES from "@/store/aes.js";
import InputV1 from "../../components/InputV1.vue";
import tinymceEditor from "../../components/tinymce-editor/tinymce-editor.vue";

export default {
  data() {
    return {
      breadcrumb: [
        {
          text: "最新消息",
          to: "/Admin_Index",
        },
        {
          text: "新增消息內容",
          active: true,
        },
      ],
      json: null,
      form1: {
        title: "消息內容",
        data: {
          NE_TITLE: {
            type: "text",
            val: "",
            text: "請填寫標題",
            only: false,
            msg: "",
          },
          NE_DATE: { type: "date", val: "", only: false, msg: "" },
          NE_CONTENT: { val: "" },
          NE_ISONLINE: {
            type: "b-switch-group",
            val: [],
            only: false,
            size: "lg",
            stacked: false,
            options: [{ value: "1", text: "" }],
            msg: "",
          },
        },
      },
    };
  },
  components: {
    InputV1,
    tinymceEditor,
  },
  created() {
    this.userSignCheck();
  },
  mounted() {
    this.form1.data["NE_DATE"].val = this.date2str(new Date()).join("-");
  },
  methods: {
    userSignCheck() {
      if (sessionStorage.getItem("TokenID") === null) {
        alert("請登入系統!!");
        this.$router.push({ path: "/Login" });
      }
    },
    //新增檢查
    checkInput() {
      this.errorText = "";
      if (this.form1.data["NE_TITLE"].val == "")
        this.errorText += "請輸入標題\n";
      if (this.form1.data["NE_DATE"].val == "")
        this.errorText += "請輸入日期\n";
      if (this.form1.data["NE_CONTENT"].val == "")
        this.errorText += "請輸入內容 \n";
      if (this.errorText !== "") alert(this.errorText);
    },
    //存入資料
    form1Save() {
      this.checkInput();
      if (this.errorText === "") {
        // var aesMStr = null;
        // aesMStr = AES.encrypt(
        //   this.form1.data["NE_CONTENT"].val
        // ); // 加密
        let json1 = {};
        json1.NE_TITLE = this.form1.data["NE_TITLE"].val;
        json1.NE_DATE = this.form1.data["NE_DATE"].val;
        // json1.NE_CONTENT = aesMStr;
        json1.NE_CONTENT = this.form1.data["NE_CONTENT"].val;
        json1.NE_ISONLINE =
          this.form1.data["NE_ISONLINE"].val.length == 0 ? 0 : 1;

        console.log(json1);
        this.json = json1;
      }
    },
  },
};
</script>

